import {Component, Mixins} from "vue-property-decorator";


@Component
export default class ConstructorHelpers extends Mixins() {
    entityName = ''
    entityNamePrefixCreate = ''
    entityNamePrefixChange = ''

    get pageName(): string { return this.$route.meta.isCreatePage ? (this.entityNamePrefixCreate ? this.entityNamePrefixCreate : '  ') + this.entityName
        : (this.entityNamePrefixChange ? this.entityNamePrefixChange : ' ') + this.entityName }
    get isCreatePage(): boolean { return this.$route.meta.isCreatePage }
}
